import fetch from './fetch.js'
import refs from './refs.js'
import template from '../template/item.hbs'
import debounce from 'lodash.debounce'
import infiniteScroll from 'infinite-scroll'
// const loadMoreBtn = document.createElement('button');
// loadMoreBtn.textContent = "Load more..."
refs.userInput.addEventListener('input', debounce((e) => {
  fetch.searchQ = e.target.value;
  refs.galleryList.innerHTML = ''
  fetch.getFetch().then((data) => render(data.hits))
  e.target.value = '';
  fetch.page += 1;

}, 1000))

function render(data) {
  const item = template(data);
  refs.galleryList.insertAdjacentHTML('beforebegin', item)
}

const infScroll = new InfiniteScroll( elem, {
  // options
  path: '.pagination__next',
  append: '.post',
  history: false,
});

